<template>
  <CentrisModal v-if="modalOpened" @close="closeModal" />
  <div class="production">
    <CentrisPreview>
      <template v-slot:title>
        <h1 class="production-title">
          Video <span class="production-title_prod">Production</span>
        </h1>
      </template>
      <template v-slot:descr>
        <p class="production-descr">
                . 
             <br />
           :     . 
           , <br />
          , .      
        </p>
      </template>
      <template v-slot:button>
        <CentrisBtn class="production_btn" @click="openModal" />
      </template>
      <template v-slot:img>
        <video
          class="preview-video"
          src="@/assets/video/main.mp4"
          autoplay="autoplay"
          loop="true"
          muted="muted"
          playsinline
        ></video>
      </template>
      <template v-slot:imgMob>
        <video
          class="preview-videoMob"
          src="@/assets/video/main-mob.mp4"
          autoplay="autoplay"
          loop="true"
          muted="muted"
          playsinline
        ></video>
      </template>
    </CentrisPreview>
    <div class="production-service">
      <div class="container">
        <div class="production-service__content">
          <div
            class="production-service__content_item"
            v-for="(item, index) in ServiceItems"
            :key="index"
          >
            <img
              class="production-service__content_item-img"
              :src="require('@/assets/images/video-images/' + item.img)"
              alt=""
            />
            <h3
              class="production-service__content_item-title"
              v-html="item.title"
            ></h3>
            <p
              class="production-service__content_item-descr"
              v-html="item.descr"
            ></p>
          </div>
        </div>
      </div>
    </div>
    <div class="production-clients">
      <div class="production-clients_content">
        <div class="production-clients_content-head">
          <h4 class="production-clients_content-head-title"> </h4>
          <p class="production-clients_content-head-descr">
             
          </p>
          <span class="production-clients_content-head-icon">
            <img src="../assets/images/video-images/arrow.png" alt="" />
          </span>
        </div>
        <div class="production-clients_content-box">
          <div
            class="production-clients_content-box_item"
            :class="{ active: openPlayer != null && openPlayer == index }"
            v-for="(item, index) in playerItems"
            :key="index"
          >
            <div class="container">
              <div
                class="production-clients_content-box_item-block"
                :class="{ active: openPlayer != null && openPlayer == index }"
              >
                <div class="production-clients_content-box_item-block-logo">
                  <img
                    class="production-clients_content-box_item-block-img"
                    :src="
                      require('@/assets/images/video-images/logo/' + item.img)
                    "
                    alt=""
                  />
                  <span class="production-clients_content-box_item-block-icon">
                    <img src="../assets/images/video-images/arrow.png" alt="" />
                  </span>
                </div>
                <div class="production-clients_content-box_item-block-txt">
                  <div
                    class="production-clients_content-box_item-block-txt-name"
                  >
                    <h5
                      class="
                        production-clients_content-box_item-block-txt-name-title
                      "
                      @click="playerOpened(index)"
                    >
                      {{ item.title }}
                    </h5>
                    <p
                      class="
                        production-clients_content-box_item-block-txt-name-descr
                      "
                    >
                      {{ item.descr }}
                    </p>
                  </div>
                  <div
                    class="production-clients_content-box_item-block-txt-icon"
                    @click="playerOpened(index)"
                  >
                    <h6
                      class="
                        production-clients_content-box_item-block-txt-icon-title
                      "
                    >
                      
                    </h6>
                    <div
                      class="
                        production-clients_content-box_item-block-txt-icon-play
                      "
                    >
                      <img
                        src="../assets/images/video-images/play.png"
                        alt=""
                      />
                    </div>
                  </div>
                </div>
              </div>
              <Transition name="player">
                <div
                  class="production-clients_content-box_item_player"
                  v-if="openPlayer == index"
                >
                  <iframe
                    class="production-clients_content-box_item_player-video"
                    :src="item.iframe"
                    frameborder="0"
                    allow="accelerometer; loop; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                    allowfullscreen
                    autoplay
                  ></iframe>
                  <swiper
                    class="production-clients_content-box_item_player-items"
                    :breakpoints="{
                      928: {
                        slidesPerView: 4,
                      },
                      769: {
                        slidesPerView: 1,
                      },
                    }"
                    modules
                    loop
                    autoplay
                  >
                    <swiper-slide
                      v-for="(items, videoIndex) in item.playerList"
                      :key="videoIndex"
                    >
                      <div
                        class="
                          production-clients_content-box_item_player-items-video
                        "
                        @click="changeVideo(index, videoIndex)"
                        :class="{ active: item.activeVideo == videoIndex }"
                      >
                        <img
                          :src="
                            require('@/assets/images/video-images/preview/' +
                              items.img)
                          "
                        />
                        <h4
                          class="
                            production-clients_content-box_item_player-items-video-title
                          "
                        >
                          {{ items.title }}
                        </h4>
                        <p
                          class="
                            production-clients_content-box_item_player-items-video-descr
                          "
                        >
                          {{ items.descr }}
                        </p>
                      </div>
                    </swiper-slide>
                  </swiper>
