    closeModal() {
      this.modalOpened = false;
    },
    playerOpened(i) {
      this.openPlayer = i;
    },
    closeOpened() {
      this.openPlayer = null;
    },
    changeVideo(playerIndex, videoIndex) {
      this.playerItems[playerIndex].iframe =
        this.playerItems[playerIndex].playerList[videoIndex].url;
      this.playerItems[playerIndex].activeVideo = videoIndex;
    },
  },
};
</script>

