<script>
import CentrisFooterSocialPages from "@/components/CentrisFooterSocialPages";
import CentrisCall from "@/components/modals/CentrisCall.vue";
export default {
  data() {
    return {
      viewportWidth: window.innerWidth,
    };
  },
  components: {
    CentrisFooterSocialPages,
    CentrisCall,
  },
  computed: {
    pageData() {
      return this.$store.getters.getPageData(this.$route.params.id);
    },
  },
  methods: {
    scrollTo(sectionId) {
      if (this.menuOpen) {
        this.menuOpen = false;
      }
      const section = document.querySelector(sectionId);
      const scrolDistance = section.offsetTop - 50;
      let distance = window.scrollY;
      const scroll = () => {
        if (scrolDistance > distance + 0.5 || scrolDistance < distance - 0.5) {
          distance -= (distance - scrolDistance) / 15;
          window.scrollTo(0, distance);
          this.timer = setTimeout(() => {
            scroll();
          }, 5);
        } else {
          window.scrollTo(0, scrolDistance);
          stop();
        }
      };
      const stop = () => {
        clearTimeout(this.timer);
        window.removeEventListener("wheel", stop);
      };
      stop();
      window.addEventListener("wheel", stop);
      scroll();
    },
    scrollToSection(sectionId) {
      if (this.$route.name != "home") {
        this.$router.push("/").then(() => {
          setTimeout(() => {
            this.scrollTo(sectionId);
          }, 300);
        });
      } else {
        this.scrollTo(sectionId);
      }
    },
    goToTop() {
      window.scrollTo(0, 0);
    },
  },
  mounted() {
    setTimeout(() => {
      window.scrollTo(0, 0);
    }, 100);
  },
};
</script>

