<template>
  <div class="footer">
    <div class="container">
      <div class="footer_content">
        <div class="footer_content-info">
          <h3 class="footer_content-info-title">  !</h3>
          <span class="footer_content-info-line"></span>
          <div class="footer_content-info-txt">
            <span class="footer_content-info-txt-span">:</span>
            <h4 class="footer_content-info-txt-contacts">
              <a href="tel:+998931113636" class="footer_content-info-txt-contacts-number">93 111 36 36</a> </h4>
          </div>
          <div class="footer_content-info-txt">
            <span class="footer_content-info-txt-span">E-mail:</span>
            <h4 class="footer_content-info-txt-contacts">info@centris.uz</h4>
          </div>
          <div class="footer_content-info-txt">
            <span class="footer_content-info-txt-span">:</span>
            <div class="footer_content-info-txt-contacts">
              <a
                class="footer_content-info-txt-contacts-add"
                href="https://yandex.uz/maps/-/CCUVyRF6lC"
                target="blank"
                rel="noopener"
                >. - 36, , </a
              >
            </div>
          </div>
          <div class="footer_content-info-txt">
            <span class="footer_content-info-txt-span"> :</span>
            <h4 class="footer_content-info-txt-contacts">
              . - .: 09:00 - 18:00
            </h4>
          </div>
        </div>
        <div class="footer_content-form">
          <form
            id="reg-course"
            class="modal__form"
            @submit.prevent
            @submit="sendFormData"
          >
            <input
              type="text"
              placeholder=" "
              name="modal-name"
              class="form__input crm-form__input"
              required
              autocomplete="off"
              v-model="modalName"
            />
            <input
              type="tel"
              placeholder=" "
              name="modal-tel"
              class="form__input crm-form__input hidden__phone"
              maxlength="17"
              v-model="modalPhone"
              @input="inputNumber"
              @focus="focusNumber"
              @blur="blurNumber"
              required
            />
            <input
              type="text"
              placeholder="Telegram @username"
              name="modal-name"
              class="form__input crm-form__input hidden__phone"
              v-model="modalUserName"
            />
            <button class="modal-btn" v-if="!submitted"> </button>
            <button class="modal-btn-accept" v-if="submitted">
                 
            </button>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      submitted: false,
      modalName: "",
      modalPhone: "",
      modalUserName: "",
    };
  },
  watch: {},
  methods: {
    sendFormData() {
      if (
        this.modalName != "" &&
        this.modalPhone != "" &&
        this.modalPhone.length == 17
      ) {
        const formData = new FormData();
        formData.append("name", this.modalName);
        formData.append("username", this.modalUserName);
        formData.append("contact", this.modalPhone);
        this.submitted = true;
        this.$store.dispatch("sendForm", formData).then((response) => {
          this.modalName = "";
          this.modalPhone = "";
          this.modalUserName = "";
          this.submitted = true;
          setTimeout(() => {
            this.submitted = false;
            this.$emit("close");
          }, 3000);
        });
      }
    },
    inputNumber(event) {
      if (this.modalPhone.length < 5) this.modalPhone = "+998 ";
      this.modalPhone = this.modalPhone.replace(/[^+|^\d|^\s]/, "");
      const lengths = [4, 7, 11, 14];
      lengths.forEach((item) => {
        if (this.modalPhone.length == item && event.data) {
          this.modalPhone += " ";
        }
      });
    },
    focusNumber() {
      if (this.modalPhone == "") this.modalPhone = "+998 ";
    },
    blurNumber() {
      if (this.modalPhone == "+998 ") this.modalPhone = "";
    },
    closeModal() {
      this.$emit("close");
    },
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.footer {
  width: 100%;
  max-width: 1700px;
  margin: 0 auto;
  background: rgba(167, 167, 167, 0.06);
  backdrop-filter: blur(15px);
  @include adaptivPaddinglg(0, 0, 120, 0, 0, 0, 80, 0);
  position: relative;
  @media (max-width: 768px) {
    @include adaptivPaddingmd(0, 0, 80, 175, 0, 0, 50,75);
  }
  &_content {
    width: 100%;
    display: flex;
    justify-content: space-between;
    @media (max-width: 768px) {
      flex-direction: column;
      align-items: center;
      margin-bottom: 30px;
    }
    &-info {
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-bottom: 50px;
      @include adaptivPaddinglg(0, 0, 0, 100, 0, 0, 0, 70);
      &-title {
        font-family: "Helvetica-bold";
        @include adaptiv-fontlg(41, 38);
        @include adaptiv-lineHeightlg(42, 39);
        color: #fff;
        @include adaptivMarginlg(0, 0, 0, 23, 0, 0, 0, 8);
        @media (max-width: 768px) {
          @include adaptiv-fontmd(38, 26);
          @include adaptiv-lineHeightmd(39, 27);
          @include adaptivMarginmd(0, 0, 0, 24, 0, 0, 0, 12);
        }
        @media (min-width: 1921px) {
          font-size: 50px;
        }
      }
      &-line {
        @include adaptivWidthLg(285, 288);
        height: 1px;
        background: #fff;
        @media (max-width: 768px) {
          @include adaptivWidthmd(215, 199);
        }
      }
      &-txt {
        width: 80%;
        @include adaptivMarginlg(0, 0, 0, 10, 0, 0, 0, 5);
        @media (min-width: 1921px) {
          margin: 0;
        }
        @media (max-width: 768px) {
          @include adaptivMarginmd(0, 0, 20, 30, 0, 0, 10, 20);
          width: 100%;
        }
        &:last-of-type {
          margin-top: 0;
        }
        &:first-of-type {
          @include adaptivMarginlg(0, 0, 31, 0, 0, 0, 22, 0);
          @media (max-width: 768px) {
            @include adaptivMarginmd(0, 0, 20, 30, 0, 0, 10, 20);
          }
        }
        &-span {
          font-family: "Helvetica-normal";
          @include adaptiv-fontlg(15, 15);
          @include adaptiv-lineHeightlg(18, 18);
          color: #8e8e8e;
          @media (max-width: 768px) {
            @include adaptiv-fontmd(25, 14);
            @include adaptiv-lineHeightmd(28, 14);
            @include adaptivMarginmd(0, 0, 0, 5, 0, 0, 0, 3);
          }
          @media (min-width: 1921px) {
            font-size: 20px;
          }
        }
        &-contacts {
          font-family: "Helvetica-normal";
          @include adaptiv-fontlg(22, 18);
          @include adaptiv-lineHeightlg(25, 21);
          color: #fff;
          @include adaptivMarginlg(0, 0, 6, 15, 0, 0, 4, 8);
          @media (max-width: 768px) {
            @include adaptiv-fontmd(30, 16);
            @include adaptiv-lineHeightmd(36, 19);
          }
          &-add, &-number {
            color: #fff;
            transition: 0.5s;
            &:hover {
              color: #7338d8;
              transition: 0.5s;
            }
          }
        }
      }
    }
    &-form {
      @include adaptivWidthLg(444, 375);
      width: 100%;
      justify-content: center;
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;
      @media (max-width: 768px) {
        width: 100%;
        @include adaptivWidthmd(550, 250);
      }
      .modal-btn,
      .modal-btn-accept {
        display: flex;
        align-items: center;
        justify-content: center;
        @include adaptivWidthLg(444, 375);
        width: 100%;
        @include adaptivHeightlg(49, 50);
        background: #2b2a29;
        color: #fff;
        @include adaptiv-fontlg(15, 15);
        @include adaptiv-lineHeightlg(15, 15);
        font-family: "Helvetica-normal";
        margin: 0 auto;
        .button-link {
          color: #303030;
        }
        &-accept {
          background: #7338d8;
          @include adaptiv-fontlg(15, 12);
        }
        @media (max-width: 768px) {
          @include adaptivWidthmd(550, 250);
          @include adaptivHeightmd(96, 40);
          @include adaptiv-fontmd(35, 14);
          @include adaptiv-lineHeightlg(35, 14);
          .modal-btn-accept {
            @media (max-width: 768px) {
              @include adaptivWidthmd(550, 250);
              @include adaptivHeightmd(96, 40);
              @include adaptiv-fontmd(25, 12);
              @include adaptiv-lineHeightlg(25, 12);
            }
          }
        }
        @media (min-width: 1921px) {
          font-size: 20px;
          height: 60px;
        }
      }
      .form__input {
        width: 100%;
