import { createRouter, createWebHistory } from "vue-router";
import Home from "@/views/Home";
import VideoProduction from "@/views/VideoProduction";
import ProjectsPages from "@/views/ProjectsPages";
import Page404 from "@/views/Page404";
import setMetas from "@/seo/index";

const projectList =
  "blackstar-carwash|pit|wella|printuz|kanstik|centrisfit|tibbiyot|bscafe";

const routes = [
  {
    path: '/Centris/',
    name: 'home',
    component: Home,
  },
  {
    path: `/Centris/project/:id(${projectList})`,
    name: 'project',
    component: ProjectsPages,
  },
  {
    path: "/Centris/video-production",
    name: "video-production",
    component: VideoProduction,
  },
  // {
  //   path: "/",
  //   name: "home",
  //   component: Home,
  // },
  // {
  //   path: "/video-production",
  //   name: "video-production",
  //   component: VideoProduction,
  // },
  // {
  //   path: `/project/:id(${projectList})`,
  //   name: "project",
  //   component: ProjectsPages,
  // },
  {
    path: "/:pathMatch(.*)*",
    name: "error",
    component: Page404,
  },
];

const router = createRouter({
  history: createWebHistory(),
  routes,
  mode: "history",
});

router.beforeEach(to => {

  setMetas(to)
  
})
export default router;
