<template>
  <div class="title-wrapper" ref="titleWrapper">
    <h2 class="title" ref="title" >
      <slot name="title"></slot>
    </h2>
    <p class="typography" ref="typography"><span class="title-name" ref="typographyText"></span><span class="title-dots" ref="typographyDots"></span></p>
  </div>
</template>

<script>
export default {
  props: {
    text: {
      type: String,
    },
  },
  methods: {
    scrollDots() {
      const parent = this.$refs.titleWrapper;
      const title = this.$refs.title;
      const typography = this.$refs.typography;
      const typographyText = this.$refs.typographyText;
      const typographyDots = this.$refs.typographyDots;
      const text = title.innerHTML;
      const distance = 300;
      const distanceCalc = (element, sum = 0) => {
        if (element) {
          sum += element.offsetTop;
          return distanceCalc(element.parentElement, sum);
        } else {
          return sum;
        }
      };
      const offsetTopDistance = distanceCalc(parent);
      typographyText.innerHTML = ""
      typographyDots.innerHTML = "".repeat(text.length);

      const calculate = () => {
        if (window.innerWidth > 768) {
          let result = Math.floor(
            text.length *
              ((window.scrollY -
                (offsetTopDistance - window.innerHeight + distance / 3)) /
                distance) +
              1
          );
          if (result >= 0 && result <= text.length) {
            typographyText.innerHTML = text.slice(0, result);
            typographyDots.innerHTML = "".repeat(text.length - result);
              
          } else if (result >= text.length) {
            typographyText.innerHTML = text;
            typographyDots.innerHTML = "";
          }
        } else {
          document.removeEventListener("scroll", calculate);
          typography.style.display = "none";
          title.style.opacity = "1";
        }
      };
      calculate();
      if (window.innerWidth > 768) {
        document.addEventListener("scroll", calculate);
      }
    },
  },
  mounted() {
    this.scrollDots();
  },
};
</script>

<style lang="scss">
@import "@/assets/scss/grid/grid";
.title-wrapper {
  width: 100%;
  position: relative;
  @include adaptivHeightlg(150, 90);
  @include adaptivMarginlg(50, 0, 0, 0, 30, 0, 0, 0);
  display: flex;
  align-items: center;
  @media (min-width: 1921px) {
    margin-bottom: 50px;
    margin-left: 50px;
    height: 100px;
  }
  @media (max-width: 768px) {
    @include adaptivMarginmd(35, 0, 0, 0, 20, 0, 0, 0);
    @include adaptivHeightmd(150, 90);
  }
}
.title-name{
  @include adaptiv-fontlg(48, 30);
  @media (min-width: 1921px) {
    font-size: 45px;
  }
}
.title-dots{
  @include adaptiv-fontlg(40, 25);
  @media (min-width: 1921px) {
    font-size: 30px;
  }
}
.title,
.typography {
  position: absolute;
  top: 0;
  left: 0;
  font-family: "Helvetica";
  font-weight: bold;
  margin: 0;
  text-transform: uppercase;
  z-index: 1;
  @media (min-width: 1921px) {
    font-size: 40px;
  }
  @media (max-width: 768px) {
    @include adaptiv-fontmd(48, 24);
  }
}
.title {
  opacity: 0;
  @media (max-width: 768px) {
    opacity: 1;
  }
}
.typography {
  @media (max-width: 768px) {
    display: none;
  }
}
</style>