<template>
  <div class="preview">
    <div class="container">
      <div class="preview_content">
        <h1 itemprop="name">
          <slot name="title"></slot>
        </h1>
        <p itemprop="description">
          <slot name="descr"></slot>
        </p>
        <slot name="button"> </slot>
      </div>
    </div>
    <div class="preview_shadow">
      <div class="preview-img" v-if="desktop">
        <slot name="img"></slot>
      </div>
      <div class="preview-imgMob" v-else>
        <slot name="imgMob"> </slot>
      </div>
    </div>
  </div>
</template>
  
  <script>
export default {
  data() {
    return {
      desktop: true,
    };
  },
  mounted() {
    const match = window.matchMedia("(max-width: 768px)");
    match.addEventListener("change", (e) => {
      this.desktop = !e.matches;
    });
    this.desktop = !match.matches;
  },
};
</script>
  
  <style lang="scss">
@import "@/assets/scss/grid/grid";
.preview {
  width: 100%;
  user-select: none;
  @include adaptivHeightlg(940, 550);
  display: flex;
  align-items: center;
  justify-content: center;
  @media (max-width: 768px) {
    width: 100%;
    @include adaptivHeightmd(980, 550);
    min-height: 100%;
  }

  &_content {
    position: relative;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  &_shadow {
    position: absolute;
    z-index: -1;
    top: 0;
    width: 100%;
    filter: brightness(0.3);
    @media (max-width: 768px) {
      height: 100%;
      filter: brightness(0.8);
    }
    @media (min-width: 1921px) {
      position: fixed;
      height: 100vh;
    }
    .preview-img {
      @include adaptivHeightlg(940, 550);
      width: 100%;
      object-fit: cover;
      @media (min-width: 1921px) {
        position: fixed;
        height: 100%;
      }
      @media (max-width: 768px) {
        display: none;
      }
    }
    .preview-imgMob {
      @media (min-width: 768px) {
        display: none;
      }
      @media (max-width: 768px) {
        filter: brightness(60%);
        display: block;
        width: 100%;
        @include adaptivHeightmd(1000, 550);
        object-fit: cover;
      }
    }
  }
}
</style>